// 移入黑名单 POST  http://test0.galio.ifeng.com/api/picus-namelist/addNameList
export function enterBlackList(params: any) {
  return request(`${ requestBasePath }/blackList/api/picus-namelist/addNameList`, {
    method: 'POST',
    body: params
  });
}

// 移出黑名单 POST http://test0.galio.ifeng.com/api/picus-namelist/removeNameList
export function outBlackList(params: any) {
  return request(`${ requestBasePath }/blackList/api/picus-namelist/removeNameList`, {
    method: 'POST',
    body: params
  });
}

