import React, { FC, useCallback, useState, useRef, useMemo } from 'react';
import {
  Button,
  message,
  Popconfirm,
  Modal,
  Row,
  Col,
  InputNumber,
} from 'antd';
import {
  productionOnOrOutLine,
  userOnOrOutLine,
  commontOnOrOutLine,
  useGetAdjustGrade,
  setHighQuality,
  cancleHighQualityComment,
} from '@/services/publicServices';
import {
  enterBlackList,
  outBlackList,
  BUSINESS_ID,
} from '@/pages/UserManage/BlackList/services';
import {
  ProductionDetailInfo,
  UserDetailInfo,
  CommontDetailInfo,
} from '@/types/types';
import { debounce } from '@/utils/util';

function Online() {
  return <span style={{ color: '#333', verticalAlign: 'middle' }}>上线</span>;
}
function Outline() {
  return <span style={{ color: 'red', verticalAlign: 'middle' }}>下线</span>;
}

// 作品上下线
interface ProductionOutlineProps {
  production: ProductionDetailInfo;
  refresh?: () => void;
  style?: any;
}
function ProductionOutline(props: ProductionOutlineProps) {
  const { production, refresh, style } = props;
  const isOnline = useMemo(() => {
    return production?.onlineStatus === 1;
  }, [production]);
  const handleOnOrOutLine = useCallback(async () => {
    try {
      const res: { code: number } = await productionOnOrOutLine({
        dramaId: production?.dramaId,
        onlineStatus: isOnline ? 0 : 1,
      });

      if (res?.code === 200) {
        message.success('操作成功！');
        refresh && refresh();
      }
    } catch (error) {}
  }, [isOnline, production, refresh]);

  return (
    <Popconfirm placement="topRight"
      title={ `确认 ${ isOnline ? '下线' : '上线' } 该作品吗` }
      onConfirm={ handleOnOrOutLine }
      okText="确定"
      cancelText="取消"
    >
      {isOnline ? (
        <Button danger={ true } style={ style }>
          下线
        </Button>
      ) : (
        <Button type="primary" style={ style }>
          上线
        </Button>
      )}
    </Popconfirm>
  );
}

// 用户上下线
interface UserOutlineProps {
  user: UserDetailInfo;
  refresh?: () => void;
  style?: any;
  isExpert?: boolean;
}
function UserOutline(props: UserOutlineProps) {
  const { user, refresh, style, isExpert } = props;
  const isOnline = useMemo(() => {
    return user?.status === 1;
  }, [user]);
  const handleOnOrOutLine = useCallback(async () => {
    try {
      const res: { code: number } = await userOnOrOutLine({
        userId: user?.userId,
        status: isOnline ? 110 : 1,
      });

      if (res?.code === 200) {
        message.success('操作成功！');
        refresh && refresh();
      }
    } catch (error) {}
  }, [isOnline, user, refresh]);

  return (
    <Popconfirm placement="topRight"
      // title={ isExpert
      //   ? `确认将该专家设置为 ${ isOnline ? '封禁' : '正常' } 吗`
      //   : `确认将 ${ isOnline ? '移入' : '移出' } 黑名单吗` }
      title={ `确认将该用户设置为 ${ isOnline ? '封禁' : '正常' } 状态吗` }
      onConfirm={ handleOnOrOutLine }
      okText="确定"
      cancelText="取消"
    >
      {isOnline ? (
        <Button danger={ true } style={ style }>
          封禁
        </Button>
      ) : (
        <Button type="primary" style={ style }>
          正常
        </Button>
      )}
    </Popconfirm>
  );
}

// 评论上下线
interface CommontOutlineProps {
  commont: CommontDetailInfo;
  refresh?: () => void;
  style?: any;
}
function CommontOutline(props: CommontOutlineProps) {
  const { commont, refresh, style, subjectType = 'default' } = props;
  const isOnline = useMemo(() => {
    return commont?.checkStatus === 1;
  }, [commont]);
  const handleOnOrOutLine = useCallback(async () => {
    try {
      const res: { code: number } = await commontOnOrOutLine({
        commentId: commont?.id,
        subjectId: commont?.subjectId,
        checkStatus: isOnline ? 2 : 1,
        subjectType,
      });

      if (res?.code === 200) {
        message.success('操作成功！');
        refresh && refresh();
      }
    } catch (error) {
      console.log('error', error);
    }
  }, [isOnline, commont, refresh]);

  return (
    <Popconfirm placement="topRight"
      title={ `确认 ${ isOnline ? '下线' : '上线' } 该评论吗` }
      onConfirm={ handleOnOrOutLine }
      okText="确定"
      cancelText="取消"
    >
      {isOnline ? (
        <Button danger={ true } style={ style }>
          下线
        </Button>
      ) : (
        <Button type="primary" style={ style }>
          上线
        </Button>
      )}
    </Popconfirm>
  );
}

// 优质评论
function GoodComment(props) {
  const {
    notGoodComment,
    useName,
    currentScore = 0,
    currentLevel = 'lv.1',
    commentId,
    subjectId,
    subjectType = '',
    refresh,
  } = props;
  const [showModel, setShowModel] = useState(false);
  const [changeScore, setChangeScore] = useState(currentScore);
  const [level, setLevel] = useState(currentLevel);
  const score = useRef(0);
  const handleCancle = async () => {
    // 取消优质评论
    try {
      await cancleHighQualityComment({
        id: commentId,
        subjectId,
        isHighQualityStatus: false,
        subjectType,
      });
    } catch (error) {
      message.error('评优失败，请稍后再试');
    }
    refresh();
  };

  const hideModal = () => {
    setShowModel(false);
  };

  const handleToGood = useCallback(async () => {
    setTimeout(async () => {
      try {
        await setHighQuality({
          id: commentId,
          subjectId,
          isHighQualityStatus: true,
          rate: score.current,
          subjectType,
        });
        message.success('操作成功');
      } catch (error) {
        message.error('评优失败，请稍后再试');
      }

      hideModal();
      refresh();
    }, 0);
  }, []);

  const show = () => {
    setShowModel(true);
  };

  const inputChange = useCallback((e) => {
    const temp = parseInt(currentScore + e);

    if (temp <= 300) {
      setLevel('lv.1');
    } else if (temp <= 900) {
      setLevel('lv.2');
    } else if (temp <= 1900) {
      setLevel('lv.3');
    } else if (temp <= 3100) {
      setLevel('lv.4');
    } else {
      setLevel('lv.5');
    }
    setChangeScore(temp);
    score.current = temp;
  }, []);

  const inputChangedDebounce = useCallback(
    debounce(inputChange, 600, false),
    []
  );

  return (
    <div>
      {notGoodComment ? (
        <Popconfirm placement="topLeft"
          title="确认该评论为优质吗？"
          onConfirm={ handleToGood }
          okText="确定"
          cancelText="取消"
        >
          <Button danger={ true }>优质</Button>
        </Popconfirm>
      ) : (
        // <Button type="primary" onClick={show}>
        //   优质
        // </Button>
        <Popconfirm placement="topRight"
          title="确认取消该评论的优质吗？"
          onConfirm={ handleCancle }
          okText="确定"
          cancelText="取消"
        >
          <Button danger={ true }>取消优质</Button>
        </Popconfirm>
      )}
      <Modal title={ `设置${ useName }的评论为优质` }
        visible={ showModel }
        onOk={ handleToGood }
        onCancel={ hideModal }
        okText="确认"
        cancelText="取消"
      >
        <Row>
          <Col span={ 6 }>当前积分：</Col>
          <Col span={ 8 }>{currentScore}</Col>
        </Row>
        <Row>
          <Col span={ 6 }>增加优质积分：</Col>
          <Col span={ 8 }>
            <InputNumber min={ 1 }
              onChange={ inputChangedDebounce }
              style={{ width: '200px' }}
            />
          </Col>
        </Row>
        <Row>
          <Col span={ 6 }>预计积分：</Col>
          <Col span={ 8 }>{changeScore}</Col>
        </Row>
        <Row>
          <Col span={ 6 }>预计等级：</Col>
          <Col span={ 8 }>{level}</Col>
        </Row>
      </Modal>
    </div>
  );
}

// 用户状态
interface UserStatusProps {
  status: number;
  style?: any;
  isExpert?: boolean;
}
function UserStatus(props: UserStatusProps) {
  const { status, style, isExpert } = props;

  return status === 1 ? (
    <span style={ style }>正常</span>
  ) : (
    <span style={{ color: 'red', ...style }}>封禁</span>
  );
  // {isExpert ? '禁用' : '黑名单'}
}

// 用户 黑名单
interface UserBlackProps {
  user: UserDetailInfo;
  refresh?: () => void;
  style?: any;
  isEnter?: boolean; // 移入黑名单
}
function UserBlack(props: UserBlackProps) {
  const { user, refresh, style, isEnter } = props;

  const handleOnOrOutLine = useCallback(async () => {
    try {
      let res: any = {};

      if (isEnter) {
        res = await enterBlackList({
          typeContent: '黑名单',
          businessId: 'com_wind_vane',
          description: user?.nickName,
          category: 'userId',
          content: user?.userId,
        });
      } else {
        res = await outBlackList({
          idList: [user.id],
        });
      }

      if (res?.code === 200) {
        message.success('操作成功！');
        refresh && refresh();
      } else {
        message.error(res?.message || '服务器出问题啦');
      }
    } catch (error) {}
  }, [isEnter, user, refresh]);

  return (
    <Popconfirm placement="topRight"
      title={ `确认将该用户 ${ isEnter ? '移入' : '移出' } 黑名单吗` }
      onConfirm={ handleOnOrOutLine }
      okText="确定"
      cancelText="取消"
    >
      {isEnter ? (
        <Button danger={ true } style={ style }>
          移入黑名单
        </Button>
      ) : (
        <Button type="primary" style={ style }>
          移出黑名单
        </Button>
      )}
    </Popconfirm>
  );
}

// 作品调控状态
interface ProductionAdjustStatusProps {
  subjectId: string;
  style?: any;
  isCol?: boolean;
}
function ProductionAdjustStatus(props: ProductionAdjustStatusProps) {
  const { isCol, style, subjectId } = props;
  const { adjustGrade, isValidating } = useGetAdjustGrade(subjectId);
  const text = useMemo(() => {
    switch (adjustGrade?.status) {
      case 0:
        return <span style={ style }>调控完成</span>;
      case 1:
        return <span style={{ color: '#1890ff', ...style }}>调控中</span>;
      default:
        return <span style={ style }>未调控</span>;
    }
  }, [adjustGrade?.status]);

  return (
    <>
      {text}
      {isCol
        ? adjustGrade?.opUser && (
        <p style={{ color: '#999' }}>{adjustGrade?.opUser || ''}最新调控</p>
      )
        : adjustGrade?.opUser && (
        <span style={{ marginLeft: 8 }}>
          ({adjustGrade?.opUser || ''}最新调控)
        </span>
      )}
    </>
  );
}

export {
  Online,
  Outline,
  ProductionOutline,
  UserOutline,
  CommontOutline,
  UserStatus,
  ProductionAdjustStatus,
  UserBlack,
  GoodComment,
};
